  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #eee;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #a5593c;
  border-radius: 30px;
  min-height: 30px;
}


/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #687a61;
}

