.navbar-containers {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 80px;
  padding-right: 80px;
  z-index: 100;
  padding-top: 30px;
  padding-bottom: 20px;
}
.navbar-containers.active{
background-color: #fff !important;
}

.containers {
  z-index: 1;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  margin-top: 20px;
}
.nav-item {
  height: 20%;
  border-bottom: 2px solid transparent;
  text-decoration: none !important;
  display: block;
}

.nav-item:hover {
  border-bottom: 2px solid #a5593c;
}
.nav-item:hover .nav-links {
  color: #a5593c;
}

.nav-links {
  color: #191919;
  display: inline-block;
  align-items: center;
  text-decoration: none !important;
  padding: 0.5rem 1rem;
  height: 100%;
}

.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
  color: #a5593c;
}
.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none !important;
  border: none !important;
  white-space: nowrap;
  cursor: pointer;
  display: inline-block;
}
.btn:focus {
  outline: none !important;
}
.btn-primary {
  background-color: #a5593c !important;
  font-size: 15px !important;
  color: #fff !important;
  border: 1px solid #a5593c !important;
  width: 130px;
  margin-left: 20px;
}
.btn-primary:hover {
  background-color: #fff !important;
  color: #191919 !important;
  border: 1px solid #a5593c !important;
}
.btn-mobile {
  text-align: center;
  border-radius: 4px;
  width: 100%;
  text-decoration: none;
  font-size: 24px;
  background-color: #a5593c !important;
  color: #fff !important;
  padding: 14px 20px;
  border: 1px solid #a5593c !important;
  transition: all 0.5s ease-in-out;
}
.btn-mobile:hover {
  transition: all 0.5s ease-in-out;
  background: #fff !important;
  color: #191919 !important;
  border: 1px solid #fff !important;
}
/* .nav_dropdown {
