} */
.dropdown-icon {
  margin-left: 5px;
  color: #a5593c;
}
.dropdown {
  position: absolute !important;
  /* display: none; */
  top: 70px;
  /* left: 50%; */
  width: 220px;
  height: 140px !important;
  background-color: #fff;
  border-radius: 10px;
  /* padding: 10px 20px; */
  transition: all 0.5s ease-in-out;
  transform: scale(0);
  z-index: 300;
}
.dropdown_active {
  /* display: block; */
  transform: scale(1);
}
.nav_dropdown_link {
  display: block;
  /* margin-bottom: 15px; */
  color: #191919 !important;
  transition: all 0.5s ease-in-out;
  text-decoration: none !important;
  width: 100%;
  padding: 10px;
  /* height: 100%; */
}
.nav_dropdown_link:hover {
  color: #fff !important;
  background-color: #a5593c !important;
}
.active {
  color: #a5593c;
  
}

@media screen and (max-width: 960px) {
  .nav-item {
    position: relative;
  }
  .navbar-logo{
    padding-left: 5px;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 60px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #eee;
    left: 0;
    opacity: 1;
    transition: all 0.6s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #f00946;
    transform: scale(1.1);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .nav-item {
    width: 100%;
  }
  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
  .navbar-containers {
